import React,{Component} from 'react'
import {
    Form,
    Input,
    Select,
    Button,
  } from 'antd';

export default class Announce extends Component {

    render(){
        return(
            <div>
                <Form.Item label="选择发布公告类型">
                <Select >
                  <Select.Option value="student">学生公告</Select.Option>
                  <Select.Option value="teach">教师公告</Select.Option>
                </Select>
                </Form.Item>
                <Form.Item label="编写公告内容">
                <Input />
                </Form.Item>
                <Form.Item >
                    <Button type="primary" htmlType="submit">提交</Button>
                </Form.Item>
            </div>
        )
    }
}

