import React,{Component} from 'react'
import { Table, Button ,Select } from 'antd';

const columns = [
    {
        title: 'Name',
        dataIndex: 'name',
    },
    {
        title: 'Age',
        dataIndex: 'age',
    },
    {
        title: 'Address',
        dataIndex: 'address',
    },
];

const data = [];
for (let i = 0; i < 46; i++) {
data.push({
    key: i,
    name: `Edward King ${i}`,
    age: 32,
    address: `London, Park Lane no. ${i}`,
});
}
// 搜索框
const { Option } = Select;

function onChange(value) {
  console.log(`selected ${value}`);
}

function onBlur() {
  console.log('blur');
}

function onFocus() {
  console.log('focus');
}

function onSearch(val) {
  console.log('search:', val);
}

export default class Cmanagement extends Component {

    state = {
        selectedRowKeys: [], // Check here to configure the default column
        loading: false,
      };
    
      del = () => {
        this.setState({ loading: true });
        // ajax request after empty completing
        setTimeout(() => {
          this.setState({
            selectedRowKeys: [],
            loading: false,
          });
        }, 1000);
      };

      add = () => {
        setTimeout(() => {
          this.setState({
            selectedRowKeys: [],
          });
        }, 1000);
      };
    
      onSelectChange = selectedRowKeys => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
      };
        

    render() {
        const { loading, selectedRowKeys } = this.state;
        const rowSelection = {
          selectedRowKeys,
          onChange: this.onSelectChange,
        };
        const hasSelected = selectedRowKeys.length > 0;
        return (
          <div>
            <div style={{ marginBottom: 16 }}>
              <Select
                showSearch
                style={{ width: 200 }}
                placeholder="输入搜索内容"
                optionFilterProp="children"
                onChange={onChange}
                onFocus={onFocus}
                onBlur={onBlur}
                onSearch={onSearch}
                filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
                >
                <Option value="jack">全部课程</Option>
                <Option value="lucy">线上课程</Option>
                <Option value="tom">线下课程</Option>
              </Select>,
              <span style={{ marginLeft: 8 }}></span>
              <Button type="primary" onClick={this.add} >
                    添加
              </Button>
              <span style={{ marginLeft: 8 }}>
              <Button type="primary" onClick={this.del} disabled={!hasSelected} loading={loading} danger>
                删除
              </Button>
              {hasSelected ? `已选择 ${selectedRowKeys.length} 项` : ''}
              </span>
              <span style={{ marginLeft: 8 }}>
                
              </span>
            </div>
            <Table rowSelection={rowSelection} columns={columns} dataSource={data} />
          </div>
        );
    }
}

