import React,{Component} from 'react'
import { Layout, Menu ,Breadcrumb} from 'antd';
import { DesktopOutlined , PieChartOutlined,} from '@ant-design/icons';
import {NavLink , Switch , Route ,Redirect} from 'react-router-dom'

import Announce from '../../pages/administrator/announce'
import Smanagement from '../../pages/administrator/smanagement'
import Tmanagement from '../../pages/administrator/tmanagement'
import Cmanagement from '../../pages/administrator/cmanagement'

import './index.css'
const { Header, Content, Footer, Sider } = Layout;

export default class Index  extends Component {
  state = {
    collapsed: false,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    return (
      <Layout style={{ minHeight:'100vh'}}>
        
        <Header className="header head">
          <div className="head-l" > SKY教育管理系统</div>
          <div className='head-r'>
          <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['2']}>
            <Menu.Item key="1">个人中心</Menu.Item>
            <Menu.Item key="2" to='/src/components/login'>退出</Menu.Item>
          </Menu>
          </div>
        </Header>
      <Layout className="site-layout-background" style={{ padding: '0' }}>
      <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item key="1" >
              <NavLink to='/announce' ><PieChartOutlined /><span>发布公告</span></NavLink>
            </Menu.Item>
            <Menu.Item key="2">
              <NavLink to='/smanagement'><DesktopOutlined /><span>学生管理</span></NavLink>
            </Menu.Item>
            <Menu.Item key="3">
              <NavLink to='/tmanagement'><DesktopOutlined /><span>教师管理</span></NavLink>
            </Menu.Item>
            <Menu.Item key="4">
              <NavLink to='/cmanagement'><DesktopOutlined /><span>课程管理</span></NavLink>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout className="site-layout">
          <Content style={{ margin: '0 16px' }}>
            <Breadcrumb style={{ margin: '16px 0' }}>
              <Breadcrumb.Item>管理员</Breadcrumb.Item>
              <Breadcrumb.Item>Davy</Breadcrumb.Item>
            </Breadcrumb>
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
              <Switch>
                <Route path='/announce' component={Announce}></Route>
                <Route path='/smanagement' component={Smanagement}></Route>
                <Route path='/tmanagement' component={Tmanagement}></Route>
                <Route path='/cmanagement' component={Cmanagement}></Route>
                <Redirect to='/announce'/>
              </Switch>
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
        </Layout>
      </Layout>
      </Layout>
    );
  }
}